@font-face {
  font-family: "flaticon";
  src: url("../assets/fonts/flaticon.ttf?4c0e27fa8dc2112db67963035d42df39")
      format("truetype"),
    url("../assets/fonts/flaticon.woff?4c0e27fa8dc2112db67963035d42df39")
      format("woff"),
    url("../assets/fonts/flaticon.woff2?4c0e27fa8dc2112db67963035d42df39")
      format("woff2"),
    url("../assets/fonts/flaticon.eot?4c0e27fa8dc2112db67963035d42df39#iefix")
      format("embedded-opentype"),
    url("../assets/fonts/flaticon.svg?4c0e27fa8dc2112db67963035d42df39#flaticon")
      format("svg");
}

i[class^="flaticon-"]:before,
i[class*=" flaticon-"]:before {
  font-family: flaticon !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.flaticon-linkedin:before {
  content: "\f101";
}
.flaticon-twitter:before {
  content: "\f102";
}
.flaticon-instagram-social-network-logo-of-photo-camera:before {
  content: "\f103";
}
.flaticon-facebook:before {
  content: "\f104";
}
.flaticon-youtube:before {
  content: "\f105";
}
